import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, ColEditDelete } from 'components/common';
import BtnAddNew from 'components/common/button/BtnAddNew';
// import BtnEdit from 'components/common/button/BtnEdit';
// import BtnDelete from 'components/common/button/BtnDelete';
import BtnExport from 'components/common/button/BtnExport';
import { Card, Row, Table, Col, message } from 'antd';
// import { classroom } from 'dump/data';
import { getBuildingRooms, deleteBuildingRoom } from 'services/buildingRoom';
import './style.scss';

// const columns = [
//   {
//     title: 'Cơ sở đào tạo',
//     dataIndex: 'groundworkTraining',
//     key: 'groundworkTraining',
//   },
//   {
//     title: 'Tòa nhà',
//     dataIndex: 'building',
//     key: 'building',
//   },
//   {
//     title: 'SL tầng',
//     dataIndex: 'floor',
//     key: 'floor',
//   },
//   {
//     title: 'SL phòng',
//     dataIndex: 'numberRoom',
//     key: 'numberRoom',
//   },
// ];

class ClassroomPage extends Component {
  state = {
    data: []
  };

  componentWillMount = async() => {
    const result = await getBuildingRooms();
    if(result.data) {
      this.setState({ data: result.data.items });
    }
  }

  handleDelete = async id => {
    await deleteBuildingRoom(id);
    message.success('Xóa thành công');
    const result = await getBuildingRooms();
    if(result.data) {
      this.setState({ data: result.data.items });
    }
  };

  expandedRoomRowRender = floor => {
    const innerColumns = [
      {
        title: 'Ký hiệu phòng',
        dataIndex: 'label',
        key: 'label',
        className: 'column-room-label'
      },
      {
        title: 'Sức chứa',
        dataIndex: 'size',
        key: 'size'
      },
      {
        title: 'Loại phòng',
        key: 'roomType'
      },
      {
        title: 'Thiết bị',
        dataIndex: 'devices',
        key: 'devices'
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        key: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#classroom/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={floor.roomList || []}
        pagination={false}
        className="room-table"
      />
    );
  };
  
  expandedFloorRowRender = buildingBase  => {
    const innerColumns = [
      { title: 'Tầng', dataIndex: 'buildingFloor', key: 'buildingFloor', className: "building-floor-column" },
      { title: 'SL phòng', dataIndex: 'noOfRoom', key: 'noOfRoom' },
    ];

    return(
      <Table
        columns={innerColumns}
        dataSource={buildingBase.floorList || []}
        pagination={false}
        expandedRowRender={this.expandedRoomRowRender}
        className="floor-table"
      />
    );
  };

  render() {
    const outerColumns = [
      { title: 'Cơ sở đào tạo', dataIndex: 'buildingBase', key: 'buildingBase', className: "building-base" },
      { title: 'Tòa nhà', dataIndex: 'building', key: 'building' },
      { title: 'SL tầng', dataIndex: 'noOfFloor', key: 'noOfFloor' },
    ];
  
    return (
      <Card title="Danh mục phòng học">
        <CartHeadAction>
          <BtnAddNew href="#/classroom/form/new" />
          <BtnExport className="btnExport" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <Table
              className="building-table"
              columns={outerColumns}
              expandedRowRender={this.expandedFloorRowRender}
              dataSource={this.state.data || []}
              bordered
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassroomPage />
  </Page>
);



