import { request } from 'utils';

const prefix = '/api/categories';


export const getBuildingRooms = ({
  label,
  size,
  roomTypeId,
  devices,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/BuildingRoom',
    method: 'GET',
    params: {
      label,
      size,
      roomTypeId,
      devices,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const getBuildingRoomById = id =>
  request({
    prefix,
    url: `/BuildingRoom/${id}`,
    method: 'GET',
    params: {},
    data: {}
  });

export const createBuildingRoom = ({floorId,label,size,roomTypeId,devices} = {}) =>
  request({
    prefix,
    url: '/BuildingRoom',
    method: 'POST',
    params: {},
    data: {floorId,label,size,roomTypeId,devices}
  });
  

export const updateBuildingRoom = ({
  id,
  floorId,
  label,
  size,
  roomTypeId,
  devices
} = {}) =>
  request({
    prefix,
    url: `/BuildingRoom/${id}`,
    method: 'PUT',
    params: {},
    data: {
      id,
      building_FloorId: floorId,
      label,
      size,
      roomTypeId,
      devices
    }
  });


export const deleteBuildingRoom = id =>
  request({
    prefix,
    url: `/BuildingRoom/${id}`,
    method: 'DELETE',
    params: {},
    data: {}
  });



